html {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

*,
*::after,
*::before {
	-webkit-box-sizing: inherit;
	        box-sizing: inherit;
}

body,
html {
	width: 100%;
	height: 100%;
}

body {
	position: relative;

	color: #0e0e0e;
	background-color: #ffffff;

	font-family: 'Open Sans', Arial, sans-serif;
	font-size: 0;
}

.wrapper {
	width: 1330px;
	min-height: 610px;
	margin: 0 auto;
}

.quiz {
	width: 1330px;
	min-height: 610px;

	&__btn {
		display: inline-block;

		padding: 0 45px;

		cursor: pointer;
		transition: 0.5s background;
		vertical-align: top;
		letter-spacing: 0.1em;
		text-transform: uppercase;

		color: #ffffff;
		border: 0;
		border-radius: 24px;
		outline: 0;
		background-color: #e76f17;

		font-family: 'Open Sans';
		font-size: 14px;
		font-weight: 300;
		line-height: 48px;

		&:hover {
			background-color: #e97e2f;
		}
	}

	&__btn--prev {
		position: relative;

		padding-left: 34px;

		cursor: pointer;
		transition: 0.5s color;

		color: rgba(0,0,0,0.5);
		border: none;
		outline: transparent;
		background-color: transparent;

		font-size: 14px;
		line-height: 18px;
		&:before {
			position: absolute;
			top: 50%;
			left: 0;

			width: 24px;
			height: 14px;

			content: '';
			transition: 0.5s background;
			transform: translateY(-50%);

			background-image: url("data:image/png;base64,");
		}

		&:hover {
			color: #0e0e0e;
			background-color: transparent;
			&::before {
				background-image: url("data:image/png;base64,");
			}
		}
	}


	.jq-checkbox {
		position: relative;

		box-sizing: border-box;
		width: 28px;
		height: 28px;
		margin-right: 15px;

		border: 2px solid #b6b6b6;

		flex-shrink: 0;

		&.warning {
			border-color: red;
		}

		&:after {
			position: absolute;
			top: 50%;
			left: 50%;

			width: 16px;
			height: 13px;

			content: '';
			cursor: pointer;
			-webkit-transition: all 0.2s;
			        transition: all 0.2s;
			-webkit-transform: translate(-50%, -50%);
			        transform: translate(-50%, -50%);

			opacity: 0;
			background-image: url("data:image/png;base64,");
		}

		&.checked {
			&:after {
				opacity: 1;
			}
		}
	}

	.jq-radio {
		display: inline-block;

		width: 28px;
		height: 28px;
		margin-right: 15px;

		cursor: pointer;
		-webkit-transition: all 0.5s ease-in-out;
		        transition: all 0.5s ease-in-out;
		vertical-align: sub;

		border: 2px solid #b6b6b6 ;
		border-radius: 50%;

		&:before {
			position: absolute;
			top: 50%;
			left: 50%;

			width: 12px;
			height: 12px;

			content: '';
			-webkit-transition: all 0.5s ease-in-out;
			        transition: all 0.5s ease-in-out;
			        transition: opacity 0.5s;
			-webkit-transform: translate(-50%, -50%);
			        transform: translate(-50%, -50%);

			opacity: 0;
			border-radius: 50%;
			background-color: #e76f17;
		}

		&.warning {
			border-color: red;
		}

		&.checked {
			&:before {
				position: absolute;
				top: 50%;
				left: 50%;

				width: 12px;
				height: 12px;

				content: '';
				-webkit-transform: translate(-50%, -50%);
				        transform: translate(-50%, -50%);

				opacity: 1;
				border-radius: 50%;
			}
		}
	}


	&__slide {
		position: relative;

		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;

		padding: 48px;
		padding-top: 42px;

		border: 1px solid #ececec;
		background-color: #ffffff;

		justify-content: space-between;
	}

	&__title {
		font-size: 30px;
		font-weight: normal;
		line-height: 36px;
	}

	&__form {
		display: flex;
		flex-direction: column;

		width: 860px;
		margin-right: 42px;
	}

	&__form-container {
		display: flex;
		flex-direction: column;
	}

	&__form-elements {
		display: flex;
		flex-direction: column;
		&--images {
			flex-direction: row;
		}
	}

	&__form-upper-text {
		margin-bottom: 10px;

		color: #0e0e0e;

		font-size: 14px;
		font-weight: 600;
		line-height: 24px;
	}

	&__form-question {
		margin: 0;

		font-size: 30px;
		line-height: 36px;
	}

	&__form-counter {
		margin-bottom: 9px;

		letter-spacing: 1px;
		text-transform: uppercase;

		font-size: 14px;
		font-weight: 700;
		line-height: 50px;

		b {
			color: #e76f17;
		}
	}

	&__form-row {
		display: flex;
		flex-direction: row;
	}

	&__form-item-row {
		display: flex;
		flex-direction: row;

		justify-content: flex-start;
	}

	&__form-item {
		flex-direction: column;

		margin-right: 22px;

		&--half {
			width: 50%;
			margin-bottom: 20px;
		}

		label {
			display: flex;
			flex-direction: column-reverse;

			cursor: pointer;

			font-size: 14px;
			line-height: 22px;
		}
	}

	&__form-img {
		margin-bottom: 18px;

		img {
			max-width: 198px;
			max-height: 198px;
		}
	}

	&__form-navigation {
		margin-top: auto;
	}

	&__form-btns {
		display: flex;

		justify-content: space-between;
	}

	&__comment {
		width: 330px;
		min-height: 517px;
		padding: 40px ;
		padding-bottom: 85px;

		background-color: #f5f5f5;

		flex-shrink: 0;
	}

	&__comment-img {
		max-width: 82px;
		max-height: 82px;
		margin-right: 20px;

		border-radius: 50%;

		align-self: flex-start;
	}

	&__comment-title {
		font-size: 20px;
		line-height: 26px;
	}

	&__comment-right {
		display: flex;
		flex-direction: column;
	}

	&__comment-author {
		display: flex;
		flex-direction: row;

		margin-bottom: 12px;
	}

	&__comment-name {
		display: block;

		margin-top: 11px;
		margin-bottom: 7px;

		font-size: 18px;
		font-weight: 600;
		line-height: 25px;
	}

	&__comment-post {
		display: block;

		margin-bottom: 15px;

		font-size: 16px;
		line-height: 26px;
	}

	&__comment-text {
		display: block;

		margin: 0;

		color: rgba(14,14,14,0.5);

		font-size: 14px;
		font-weight: normal;
		line-height: 22px;
	}

	&__main-slide {
		position: relative;

		display: flex;
		flex-direction: column;

		width: 100%;
		min-height: 611px;

		justify-content: center;
		&:after {
			position: absolute;
			z-index: 0;
			top: 0;
			left: 0;

			width: 100%;
			height: 100%;

			content: '';

			background-color: rgba(26,26,24,0.9);
		}
	}

	&__main-slide-content {
		position: relative;
		z-index: 1;

		max-width: 960px;
		margin: 0 auto;

		text-align: center;
	}
	&__main-slide-title {
		color: #ffffff;

		font-size: 44px;
		font-weight: normal;
		line-height: 48px;
	}

	&__main-slide-desc {
		margin-bottom: 65px;

		color: #ffffff;

		font-size: 23px;
		font-weight: 600;
		line-height: 30px;
	}

	&__last-slide {
		position: relative;

		display: flex;
		flex-direction: column;

		min-height: 611px;
		padding: 0 50px;

		text-align: center;

		background-color: #f5f5f5;

		justify-content: center;
	}

	&__last-slide-header {
		display: flex;
		flex-direction: column;
	}

	&__last-slide-icon {
		display: block;

		width: 58px;
		height: 58px;
		margin-bottom: 22px;

		background-image: url('data:image/png;base64,');

		align-self: center;
	}

	&__last-slide-title {
		margin: 0;
		margin-bottom: 29px;

		color: #0e0e0e;

		font-size: 30px;
		font-weight: 400;
		line-height: 36px;
	}

	&__last-slide-comment {
		max-width: 680px;
		margin-bottom: 45px;

		color: #0e0e0e;

		font-size: 14px;
		line-height: 22px;

		align-self: center;
		p {
			margin: 0;
		}
		img {
			max-width: 632px;
			max-height: 147px;
		}
	}

	&__contacts-form {
		display: flex;
		flex-direction: column;
	}

	&__contacts-form-row {
		display: flex;
		flex-direction: row;

		margin-bottom: 30px;

		justify-content: center;
	}

	&__contacts-form-column {
		display: flex;
		flex-direction: column;

		justify-content: center;
	}

	&__contacts-form-elem {
		width: calc(100% / 3);
		margin-right: 21px;

		&:last-child {
			margin-right: 0;
		}
	}

	&__contacts-form-input {
		width: 100%;
		height: 48px;
		padding-right: 17px;
		padding-left: 17px;

		color: #0e0e0e;
		border: 2px solid #ececec;
		outline-color: transparent;
		box-shadow: none;

		font-family: 'Open Sans';
		font-size: 14px;
		font-weight: 400;
		line-height: 48px;
		&.warning {
			border-color: red;
		}
	}

	&__contacts-form-accuracy {
		display: flex;
		flex-direction: row;

		margin-bottom: 18px;

		align-items: center;
		justify-content: center;
	}

	&__contacts-form-checkbox-label {
		color: #0e0e0e;

		font-size: 14px;
		line-height: 24px;

		a {
			transition: border-color 0.5s;
			text-decoration: none;

			color: #0e0e0e;
			border-bottom: 1px solid #e76f17;

			font-size: 14px;
			line-height: 24px;

			&:hover {
				border-color: transparent;
			}
		}
	}

	&__contacts-form-btn {
		align-self: center;
	}

	&__thanks {
		position: relative;

		display: flex;
		flex-direction: column;

		min-height: 611px;

		text-align: center;

		background-color: #f5f5f5;

		justify-content: center;
		align-items: center;
	}

	&__thanks-icon {
		display: block;

		width: 54px;
		height: 69px;
		margin: 0 auto;
		margin-bottom: 22px;

		background-image: url('data:image/png;base64,');

		align-self: center;
	}

	&__thanks-title {
		color: #0e0e0e;

		font-size: 30px;
		font-weight: normal;
		line-height: 36px;
	}

	&__thanks-text {
		p {
			color: #0e0e0e;

			font-size: 14px;
			font-weight: normal;
			line-height: 22px;
		}
	}

	&__select-slide {
		position: relative;

		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;

		min-height: 611px;

		border: 1px solid #ececec;
		background-color: #ffffff;
	}

	&__select-slide-left {
		padding: 48px;
	}

	&__select-slide-right {
		display: flex;
		flex-direction: column;

		width: 494px;
		padding: 0 45px;

		background-color: #f5f5f5;

		justify-content: center;
		flex-shrink: 0;
	}

	&__select-slide-btn {
		margin-top: 10px;

		align-self: flex-start;
	}

	&__select-slide-info {
		margin: 0;
		margin-bottom: 34px;

		color: #0e0e0e;

		font-size: 20px;
		line-height: 26px;
	}

	&__select-slide-row-input {
		width: 100%;
		height: 48px;
		padding-left: 18px;

		color: #222222;
		border: 2px solid #ededed;
		outline-color: transparent;

		font: 'Open Sans';
		font-size: 14px;
		line-height: 48px;

		&.warning {
			border-color: red;
		}
	}

	&__select-slide-right-row {
		display: flex;

		margin-bottom: 20px;
	}

	&__select-slide-checkbox-label {
		color: #222222;

		font-size: 14px;
		line-height: 28px;

		a {
			transition: border-color 0.5s;
			text-decoration: none;

			color: #222222;
			border-bottom: 1px solid #e76f17;

			font-size: 14px;
			line-height: 0;


			&:hover {
				border-color: transparent;
			}
		}
	}

	&__select-slide-left {
		display: flex;
		flex-direction: column;

		padding-top: 43px;
	}

	&__select-slide-title {
		margin: 0;
		margin-bottom: 53px;

		color: #0e0e0e;

		font-size: 30px;
		font-weight: normal;
		line-height: 36px;
	}

	&__select-slide-row {
		display: flex;
		flex-direction: row;

		margin-bottom: 30px;

		align-items: center;
	}

	&__select-slide-label {
		width: 100%;
		max-width: 151px;

		color: #0e0e0e;

		font-size: 14px;
		line-height: 23px;
	}

	&__select-slide-cont {
		span {
			padding-left: 9px;

			color: #0e0e0e;

			font-size: 14px;
			line-height: 36px;
		}
	}

	&__select-slide-cont {
		display: flex;
		flex-direction: row;

		align-self: center;
	}

	&__select-slide-inpit {
		width: 90px;
		height: 36px;
		padding-left: 11px;

		border: 2px solid #b6b6b6;
		outline-color: transparent;

		font-size: 14px;
		line-height: 36px;

		&.warning {
			border-color: red;
		}
	}

	&__select-slide-select {
		&.warning {
			.jq-selectbox__select {
				border-color: red;
			}
		}
	}


	.jq-selectbox__select {
		width: 438px;
		height: 36px;

		transition: border-color 0.4s;

		border: 2px solid #b6b6b6;
	}

	.jq-selectbox__select-text {
		width: 100%!important;
		padding-left: 11px;

		text-align: left;

		color: #0e0e0e;

		font-family: 'Open Sans';
		font-size: 14px;
		font-weight: normal;
		line-height: 1.56;
		line-height: 36px;
	}

	.jq-selectbox__dropdown {
		top: 36px!important;

		width: 100%;
	}

	.jq-selectbox__dropdown ul {
		padding-left: 0;

		box-shadow: 0 6px 32px rgba(0,0,0,0.21);
	}

	.jq-selectbox__dropdown ul li {
		height: 36px;
		padding-left: 11px;

		text-align: left;
		vertical-align: middle;

		color: #0e0e0e;
		background-color: #ffffff;

		font-family: 'Open Sans';
		font-size: 14px;
		font-weight: normal;
		line-height: 36px;

		&:hover {
			color: #ffffff;
			background-color: #000000;
		}
	}

	.jq-selectbox {
		position: relative;

		cursor: pointer;

		&::before {
			position: absolute;
			z-index: 0;
			top: 13px;
			right: 11.5px;

			width: 16px;
			height: 10px;

			content: '';
			transition: all 0.5s;

			background-image: url("data:image/png;base64,");
		}

		&.opened {
			&::before {
				z-index: 100;

				background-image: url("data:image/png;base64,");
			}
		}

		&.opened {
			&::before {
				transform: rotate(180deg);
			}
		}
	}

	&__select-slide-file {
		position: relative;

		margin-bottom: 6px;
		padding-left: 30px;

		cursor: pointer;

		align-self: flex-start;
	}

	&__select-slide-column {
		display: flex;
		flex-direction: column;
	}

	&__select-slide-file-text {
		display: block;

		padding-left: 30px;

		color: rgba(14,14,14,0.5);

		font-size: 12px;
		line-height: 15px;
	}

	.jq-file__name {
		color: #0e0e0e;
		border-bottom: 1px dotted #e87623;

		font-size: 14px;
		font-weight: 600;
		line-height: 23px;

		align-self: flex-start;
	}

	.jq-file__browse {
		position: absolute;
		top: 8px;
		left: 0;

		width: 21px;
		height: 10px;

		background-image: url('data:image/png;base64,');
	}
}

.show {
	display: flex;
}
